// const [{ preview, original, description }] = images;
const listGalary = document.querySelector('.gallery');
for (const { preview, original, description } of images) {
  const htmlFragment = `<li class="gallery-item">
	<a class="gallery-link" href=${original}>
		<img 
			class="gallery-image" 
       src=${preview}
   
      alt=${description}
			/>
	</a>
</li>`;
  listGalary.insertAdjacentHTML('afterbegin', htmlFragment);
}
let gallery = new SimpleLightbox('.gallery a', {
  captions: true,
  captionsData: 'alt',
  captionDelay: 250,
});
gallery.on('show.simplelightbox', function (event) {
  event.preventDefault();
});
